<template>
    <div :style="{top: distance + 'px'}" id="articleBox" ref="card" @mouseenter="move($event)">
        <h3><span class="glyphicon glyphicon-erase"></span></h3>
        <h3>{{title}}</h3>
        <p>{{content}}</p>
    </div>
</template>
<script>
export default {
    data: function() {
        return {
            title: 'Javascript',
            content: '通过 javascript 实现的一个js数据结构库，包含栈，队列，优先级队列，链表，双向链表等',
            distance: 0,
            arrt: []
        }
    },
    methods: {
        move(event) {
            this.distance = 0
            let timer = setInterval(() => {
                this.distance--
                if(this.distance < -13) {
                    clearInterval(timer)
                    this.distance = -13
                }
            }, 5);
            
        }
    }
}
</script>
